import QRCode from "qrcode.react";
import React from "react";
import styled from "styled-components";
import { useTheme } from "./theme";

const QRCodeContainer = styled.div`
    display: block;
`;

declare class QRCodeWrapperProps {
    public password?: string;
}

export default function QRCodeWrapper({ password }: QRCodeWrapperProps): JSX.Element {
    if (password) {
        return (
            <QRCodeContainer>
                <QRCode value={password} />
            </QRCodeContainer>
        );
    }

    const theme = useTheme();

    return (
        <QRCodeContainer>
            <QRCode value={"invalid"} bgColor="white" fgColor={theme.disabledQRCode} />
        </QRCodeContainer>
    );
}
